<template>
	<div class="login-panel">
		<div class="lp-title">
			后台管理系统
		</div>
		<div class="login">
			<el-form :model="loginData" :rules="rules" ref="loginData">
				<el-form-item prop="username">
					<el-input type="text" placeholder="username" v-model="loginData.username"></el-input>
				</el-form-item>
				<el-form-item prop="password">
					<el-input type="password" placeholder="password" v-model="loginData.password"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" :loading="isLogin" v-on:click="login('loginData')">登录</el-button>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>
<script>
	export default {
		data() {
			var checkUserName = (rule, value, callback) => {
				if(value === '') {
					callback(new Error('请输入用户名'));
				} else {
					callback();
				}
			};
			var checkPassWord = (rule, value, callback) => {
				if(value === '') {
					callback(new Error('请输入密码'));
				} else {
					callback();
				}
			};
			return {
				isLogin: false,
				loginData: {
					'username': '',
					'password': '',
				},
				rules: {
					'username': [
						{ validator: checkUserName, trigger: 'blur' }
					],
					'password': [
						{ validator: checkPassWord, trigger: 'blur' }
					],
				}
			};
		},
		methods: {
			login(formName) {
				this.$refs[formName].validate((valid) => {
					if(valid) {
						this.isLogin = !this.isLogin;
						this.$router.push('/home');
					} else {
						alert("登录失败")
						return false;
					}
				});
			}
		}
	}
</script>
<style scoped>
	.login{
		box-sizing: content-box;
	}
	.login-panel {
		position: relative;
		width: 100%;
		height: 100%;
		background: #324157;
	}

	.lp-title {
		position: absolute;
		top: 50%;
		width: 100%;
		margin-top: -230px;
		text-align: center;
		font-size: 30px;
		color: #fff;
	}

	.login {
		position: absolute;
		left: 50%;
		top: 50%;
		width: 300px;
		height: 160px;
		margin: -150px 0 0 -190px;
		padding: 40px;
		border-radius: 5px;
		background: #fff;
	}

	.el-button {
		width: 100%;
	}
</style>


